import React from 'react';
import { Routes, Route, Link } from 'react-router-dom';
import { ShellBar, ShellBarItem } from '@ui5/webcomponents-react';
import Home from './pages/Home';
import About from './pages/About';
import Products from './pages/Products';

const App = () => {
    return (
        <>
            <ShellBar
                primaryTitle="Fiori Demo"
                logo={<img src="https://sap.github.io/ui5-webcomponents/assets/images/sap-logo-svg.svg" alt="logo" />}
            >
                <ShellBarItem text="Home" icon="home" onClick={() => (window.location.href = process.env.PUBLIC_URL + '/')} />
                <ShellBarItem text="About" icon="hint" onClick={() => (window.location.href = process.env.PUBLIC_URL + '/about')} />
                <ShellBarItem text="Products" icon="product" onClick={() => (window.location.href = process.env.PUBLIC_URL + '/products')} />
            </ShellBar>

            <div style={{ padding: '1rem' }}>
                <Routes>
                    <Route path="/" element={<Home />} />
                    <Route path="/about" element={<About />} />
                    <Route path="/products" element={<Products />} />
                </Routes>
            </div>
        </>
    );
};

export default App;